body {
    font-family: "Arial", sans-serif;
    background: linear-gradient(135deg, #1e0139, #2b044d);
    color: #fff;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.form-container {
    width: 400px;
    background-color: #1e0139;
    padding: 20px;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

header h1 {
    font-size: 1.8rem;
    margin-bottom: 10px;
}

header p {
    margin: 5px 0;
    font-size: 0.9rem;
    line-height: 1.5;
}

form {
    margin-top: 20px;
}

.upload-section {
    border: 2px dashed #a857fd;
    padding: 15px;
    border-radius: 5px;
    margin-bottom: 15px;
    position: relative;
}

.upload-label {
    display: block;
    cursor: pointer;
}

.upload-section input[type="file"] {
    opacity: 0;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    cursor: pointer;
}

.upload-section p {
    font-size: 0.8rem;
    color: #ddd;
}

.form-group {
    margin-bottom: 15px;
    text-align: left;
}

.form-group label {
    display: block;
    font-size: 0.9rem;
    margin-bottom: 5px;
}

.form-group input {
    width: 100%;
    padding: 8px;
    border: none;
    border-radius: 4px;
    outline: none;
}

form button {
    background-color: #ff5722;
    color: #fff;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    font-size: 1rem;
    cursor: pointer;
}

form button:hover {
    background-color: #e64a19;
}


/* Responsive Design */

@media (max-width: 768px) {
    /* Form Container */
    .form-container {
        width: 90%;
        padding: 15px;
    }
    /* Header Styling */
    header h1 {
        font-size: 1.5rem;
    }
    header p {
        font-size: 0.8rem;
    }
    /* Form Section */
    .upload-section {
        padding: 10px;
    }
    .form-group input {
        padding: 6px;
    }
    form button {
        font-size: 0.9rem;
        padding: 8px 10px;
    }
}

@media (max-width: 480px) {
    /* Form Container */
    .form-container {
        width: 95%;
        padding: 10px;
    }
    /* Header Styling */
    header h1 {
        font-size: 1.3rem;
    }
    header p {
        font-size: 0.75rem;
    }
    /* Form Section */
    .upload-section {
        padding: 8px;
    }
    .form-group label {
        font-size: 0.8rem;
    }
    .form-group input {
        padding: 5px;
        font-size: 0.85rem;
    }
    form button {
        font-size: 0.8rem;
        padding: 6px 8px;
    }
}